body {
  /* generated */
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* end of generated */
  border: 2px solid;
  box-sizing: border-box;
  height: 100vh;
  color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Pure CSS */

.container {
  display: flex;
  width: 100%;
  max-width: 960px;
  margin: auto;
  flex-direction: column;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.sku-filters {
  padding: 8px;
  width: 100%;
  max-width: 480px;
  display: flex;
  flex-direction: column;
  margin-bottom: 24px;
}

.sku-filters-search {
  width: 100%;
  height: 32px;
  padding: 0 8px;
  border: 1px solid darkcyan;
  /*outline: 1px solid rgba(0, 139, 139, 0.2);*/
  color: #282c34;
}

.sku-filters-search:focus-visible {
  outline: 3px solid rgba(0, 139, 139, 0.2);
}

.sku-filter-in-stock {
  margin-right: 8px;
}

.form-group {
  display: flex;
  margin-bottom: 8px;
}

.text-mid {
  font-size: 18px;
}

/* TABLE */
.app-table {
  width: 100%;
  max-width: 300px;
  border-top: 2px solid;
  border-bottom: 2px solid;
}

.app-table-row-like-heading {
  font-weight: bold;
  text-align: center;
}

.at_table-head {
  text-align: left;
  padding: 2px 6px;
}

.atrlh_cell {
  border-bottom: 1px solid;
  width: calc(300px - 10%);
  color: #aaa;
}

.app-table td {
  padding: 2px 6px;
}

.skus-table-out-of-stock {
  color: #f00;
}

